.winner {
  background: #FBFBFB;
  width: 100%;
  max-width: 500px;
  padding: 10px 20px;
  display: flex;
  margin: 0 auto;
}

.left-col {
  margin-right: 40px;
}
.right-col {
  font-family: Arial;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;

  &__title {
    color: #800000;
  }
  &__prize {
    color: #000;
    margin-top: 10px;
  }
  &__button {
    margin-top: 10px;
  }
}