// Основные стили приложения
* {
  box-sizing: border-box;
}

html, body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  line-height: 0;
  font-size: 0;
}

#root {
  margin: auto;
  padding-top: 30px;
}

.wheel-fortune {
  position: relative;
  width: 80vmin;
  padding-top: 80vmin;
  margin: 0 auto;
  .wheel-fortune-inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
.row {
  &__button {
    padding: 10px;
    text-align: center;
    position: relative;
    z-index: 9;
  }
  &__input {
    text-align: center;
  }
}
